.rrze-green-office {
	&.co2-emissions-calculator {
		.input-container {
			border: 1px solid var(--color-ContentBorders, #dfe6ec);
			width: 100%;
			padding: var(--width-gutter, 30px);
			margin-top: 0;
			display: flex;
			flex-wrap: wrap;
			column-gap: var(--width-gutter, 30px);
			row-gap: var(--width-gutter-half, 15px);
			align-items: end;
			margin-bottom: 1em;
			input {
				height: 40px;
				padding-top: 9px;
				padding-bottom: 8px;
				margin: 0;
			}
			select,
			button {
				margin: 0;
			}
		}
		.chart-container {
			display: flex;
			flex-direction: row;
			flex-wrap: wrap;
			.chart-item {
				flex: 50% 1 0;
			}
		}
	}
}

